  .swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .swiper-button-prev,
  .swiper-button-next {
    color: white;
  }
  
  :root {
    --background-dark: #2d3548;
    --text-light: rgba(255, 255, 255, 0.6);
    --text-lighter: rgba(255, 255, 255, 0.9);
    --spacing-s: 8px;
    --spacing-m: 16px;
    --spacing-l: 24px;
    --spacing-xl: 32px;
    --spacing-xxl: 64px;
    // --width-container: 1200px;
  }
  
  .swiper-slider__wrapper {
    list-style: none;
    max-width: 100%;
  }
  
  .swiper-slider__wrapper:before {
    content: '';
    display: block;
    box-shadow: 0px 0px 34px 1px rgba(255, 204, 0, 1);
  }
  
  .slide-img {
    background-size: contain;
    background-position: center;
    filter: brightness(1.2) saturate(1) contrast(1);
    transform-origin: center;
    transform: scale(1) translateZ(0);
    transition: filter 200ms linear, transform 200ms linear, scale 250ms;
  }
  
  .image-slider__wrapper:hover > .swiper-slider__wrapper:not(:hover) .slide-img {
    filter: brightness(.6) saturate(1) contrast(1) blur(1px);
  }
  
  @media screen and (max-width: 767.98px) {
    .slider-container {
      display: none;
    }
  }
  
  .slide-img:hover {
    transform: scale(1.08) translateZ(0);
  }
  
